.store {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    gap: 1rem;
    margin: 1rem;
}

.filters {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    max-height: 100vh;
}

.filter__header, .shoes__header, .cart__header {
    display: flex;
}

.colors {
    display: flex;
    gap: 0.6rem;
}

.shoes {
    padding: 1.3rem;
    max-height: 100vh;
}

.shoes__array {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
}

.cart {
    padding: 1.3rem;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    padding-left: 0.7rem;
}

.cart__item {
    display: flex;
    margin-left: 0;
    gap: 1rem;
    margin-top: 2rem;
    padding-left: 0;
}